import * as actionTypes from '../actionTypes/toolbar';

export const setSelectedButton = string => ({
  type: actionTypes.SET_SELECTED_BUTTON,
  string,
});

export const setToolbarOptions = array => ({
  type: actionTypes.SET_TOOLBAR_OPTIONS,
  array,
});

export const toggleHelp = () => ({
  type: actionTypes.TOGGLE_HELP,
});

export const setHelp = bool => ({
  type: actionTypes.SET_HELP,
  bool,
});

export const disableHelpButton = bool => ({
  type: actionTypes.DISABLE_HELP_BUTTON,
  bool,
});
