/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { withRouter } from "react-router-dom";
import {
  makeStyles,
  Typography,
  TextField,
  Button,
  InputAdornment,
  IconButton,
  Tooltip
} from "@material-ui/core";
import { useSwipeable } from "react-swipeable";

import { ToggleButton, ToggleButtonGroup } from "@material-ui/lab";
import SearchIcon from "@material-ui/icons/Search";
import Clear from "@material-ui/icons/Clear";
import {
  SEARCH,
  TOOLBAR_HEIGHT,
  TITLE,
  ARTIST,
  BOTH,
  RESULTS_PATH
} from "../../constants/index";

import logo from "../../assets/images/logo-karaoke-png.png";

const useStyles = makeStyles(theme => ({
  search: {
    minHeight: `calc(100vh - ${TOOLBAR_HEIGHT}px - ${theme.spacing(3)}px)`
  },
  img: {
    maxWidth: "100%"
  },
  centerContent: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    marginBottom: theme.spacing(4)
  },
  textField: {
    maxWidth: "75vw",
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  label: {
    display: "flex",
    alignItems: "center",
    margin: theme.spacing(2)
  },
  buttonGroup: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  button: {
    height: "100%",
    minHeight: 56,
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  }
}));

const Search = props => {
  const {
    setSelectedButton,
    history,
    swipeLeftPath,
    swipeRightPath,
    search,
    showHelp,
  } = props;
  const classes = useStyles();
  let timeout;

  const inactivityTimeout = () => setTimeout(() => history.push('/'), 60000);

  React.useEffect(() => {
    timeout = inactivityTimeout();
    return () => clearTimeout(timeout);
  }, []);

  const resetTimeout = () => {
    clearTimeout(timeout);
    timeout = inactivityTimeout();
  }

  const [searchString, setSearchString] = React.useState("");
  const [searchBy, setSearchBy] = React.useState(BOTH);

  const handlers = useSwipeable({
    onSwipedLeft: () => history.push(swipeLeftPath),
    onSwipedRight: () => history.push(swipeRightPath),
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  React.useEffect(() => {
    setSelectedButton(SEARCH);
    return () => {
      setSelectedButton(null);
    };
  }, []);

  const handleSearchByOnChange = value => {
    if (value === null) {
      return;
    }

    setSearchBy(value);
    resetTimeout();
  };

  const handleKeypress = e => {
    const { key, target } = e;
    const { value } = target;
    if (key === 'Enter') {
      search(searchString, searchBy);
      history.push(RESULTS_PATH);
    }
    setSearchString(value)
    resetTimeout();
  }

  return (
    <div id="search" className={classes.search} {...handlers}>
      <div className={classes.centerContent}>
        <img src={logo} alt="logo" className={classes.img} />
      </div>
      <div id="search=box" className={classes.centerContent}>
        <Tooltip title="2. Enter your search." open={showHelp} placement="top">
          <TextField
            id="outlined-multiline-flexible"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    disabled={!searchString}
                    onClick={() => setSearchString("")}
                  >
                    <Clear />
                  </IconButton>
                </InputAdornment>
              )
            }}
            label="Search"
            value={searchString}
            onChange={handleKeypress}
            className={classes.textField}
            variant="outlined"
            fullWidth
            autoFocus
            onKeyPress={handleKeypress}
          />
        </Tooltip>
      </div>
      <Tooltip title={"3. Select a search option and Submit"} open={showHelp}>
        <div className={classes.centerContent}>
          <ToggleButtonGroup
            size="large"
            value={searchBy}
            exclusive
            onChange={(event, value) => handleSearchByOnChange(value)}
            className={classes.buttonGroup}
          >
            <ToggleButton key="title" value={TITLE}>
              <Typography>Title</Typography>
            </ToggleButton>
            <ToggleButton key="artist" value={ARTIST}>
              <Typography>Artist</Typography>
            </ToggleButton>
            <ToggleButton key="both" value={BOTH}>
              <Typography>Both</Typography>
            </ToggleButton>
          </ToggleButtonGroup>
          <Button
            className={classes.button}
            variant="outlined"
            disabled={searchString.length < 3}
            onClick={() => {
              search(searchString, searchBy);
              history.push(RESULTS_PATH);
            }}
          >
            Submit
        </Button>
        </div>
      </Tooltip>
    </div>
  );
};

export default withRouter(Search);
