import { connect } from "react-redux";
import Results from "./Results";
import { setSelectedButton, clearSearchResults, setHelp, disableHelpButton } from "../../store/actions/index";

const mapStateToProps = state => {
  const { results, api } = state;
  const { searchResults, currentSearch } = results;
  const { gettingSearchResults } = api;
  return {
    searchResults,
    gettingSearchResults,
    currentSearch,
  };
};

const mapDispatchToProps = dispatch => ({
  setSelectedButton: string => dispatch(setSelectedButton(string)),
  clearSearchResults: () => dispatch(clearSearchResults()),
  turnOffHelp: () => dispatch(setHelp(false)),
  disableHelpButton: bool => dispatch(disableHelpButton(bool)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Results);
