import { connect } from 'react-redux';
import Toolbar from './Toolbar';
import { showLogonDialog, toggleHelp } from '../../store/actions/index';

const mapStateToProps = state => {
  const { admin, toolbar } = state;
  const { djModeActive } = admin;
  const { toolbarOptions, showHelp, disableHelp } = toolbar;

  return {
    toolbarOptions,
    djModeActive,
    showHelp,
    disableHelp,
  }
}

const mapDispatchToProps = dispatch => ({
  handleLogonClick: () => dispatch(showLogonDialog(true)),
  toggleHelp: () => dispatch(toggleHelp()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Toolbar);