import * as actionTypes from '../actionTypes/toolbar';

const initialState = {
  selectedButton: null,
  toolbarOptions: [],
  showLogonButton: true,
  showHelp: false,
  disableHelp: false,
};

const setSelectedButton = (state, action) => ({
  ...state,
  selectedButton: action.string,
});

const setToolbarOptions = (state, action) => ({
  ...state,
  toolbarOptions: action.array,
});

const toggleHelp = state => ({
  ...state,
  showHelp: !state.showHelp,
});

const setHelp = (state, action) => ({
  ...state,
  showHelp: action.bool,
});

const disableHelpButton = (state, action) => ({
  ...state,
  disableHelp: action.bool,
});

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_SELECTED_BUTTON:
      return setSelectedButton(state, action);
    case actionTypes.SET_TOOLBAR_OPTIONS:
      return setToolbarOptions(state, action);
    case actionTypes.TOGGLE_HELP:
      return toggleHelp(state);
    case actionTypes.SET_HELP:
      return setHelp(state, action);
    case actionTypes.DISABLE_HELP_BUTTON:
      return disableHelpButton(state, action);
    default:
      return state;
  }
};

export default reducer;