import React from "react";
import { makeStyles, AppBar, IconButton, Tooltip } from "@material-ui/core";
import { Lock, LockOpen, Help, HelpOutline } from "@material-ui/icons";
import { TOOLBAR_HEIGHT } from "../../constants/index";
import ToolbarButton from "../ToolbarButton/ToolbarButtonConnector";

const useStyles = makeStyles(theme => ({
  appBar: {
    height: TOOLBAR_HEIGHT,
    display: "flex",
    flexDirection: "row",
    alignItems: "center"
  },
  toolbarContent: {
    height: "100%",
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexGrow: 1
  },
  iconButton: {
    height: 48,
    width: 48
  },
}));

const Toolbar = props => {
  const { toolbarOptions, djModeActive, handleLogonClick, showHelp, toggleHelp, disableHelp } = props;
  const classes = useStyles();

  return (
    <AppBar className={classes.appBar} position="fixed">
      <IconButton className={classes.iconButton} onClick={toggleHelp} disabled={disableHelp}>
        {showHelp ? <Help color="primary" /> : <HelpOutline />}
      </IconButton>
      <Tooltip title="1. Select a search method" open={showHelp}>
        <div className={classes.toolbarContent}>
          {toolbarOptions.map(obj => (
            <ToolbarButton key={obj.buttonType} {...obj} />
          ))}
        </div>
      </Tooltip>
      <IconButton className={classes.iconButton} onClick={handleLogonClick}>
        {djModeActive ? <LockOpen /> : <Lock />}
      </IconButton>
    </AppBar>
  );
};

export default Toolbar;
